import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Button, Table } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import Header from "../Header";

import ClipLoader from "react-spinners/ClipLoader";
import axios from "axios";
import { withRouter } from 'react-router-dom';
import S3 from 'react-aws-s3';
import moment from 'moment';

import 'react-calendar/dist/Calendar.css';
import DatePicker from "react-datepicker";
const dotenv = require('dotenv');
//import {CabTypes} from '../CabTypes';
class SpecialPrices extends Component {

    state = {
        userId: '', item: [], error: '', isLoading: false, loadingColor: '#ffffff', pageNo: 0,type:'',startDate:new Date(),startDateSelected:'',endDateSelected:'',endDate:new Date(),
        extraRate: 0, cabType: '', rate: 0, returTripRate: 0,cabTypes:[],isWeekday:false,weekDays:[],weekday:0,priceId:'' 
    };
    constructor(props) {
        super(props);          
    }

    componentDidMount() {
        this.setState({ isLoading: true });
        let userId = localStorage.getItem("userId");
        this.setState({ userId: userId });
        this.state.weekDays[0]='Sunday';
        this.state.weekDays[1]='Monday';
        this.state.weekDays[2]='Tuesday';
        this.state.weekDays[3]='Wednesday';
        this.state.weekDays[4]='Thursday';
        this.state.weekDays[5]='Friday';
        this.state.weekDays[6]='Saturday';
        this.getSpecailPrice(userId);
        this.getCabTypes(userId);
    }

    async getSpecailPrice(userId) {
        try {
            this.setState({cabId:0})
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + userId + "&pageId=" + pageNo;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_specialprice?' + urlData, { headers });
            
            if (cabData.data.code == 200) {
                
                this.setState({ item: cabData.data.data });
            } else {

            }
        } catch (err) {
           
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    async getCabTypes(userId) {
        try {
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + userId;
            let cabTypesData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_cab_types?' + urlData, { headers });
            
            if (cabTypesData.data.code == 200) {
                
                this.setState({ cabTypes: cabTypesData.data.data });
            } else {

            }
        } catch (err) {
           
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
        this.setState({ isLoading: false });

    }
    async viewPrice(priceId) {
        try {
            this.setState({ isLoading: true });
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&priceId=" + priceId;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_specialpricebyid?' + urlData, { headers });
            
            if (cabData.data.code == 200) {
                let newStartDate='';
                let newEndDate='';
                //this.setState({ item: cabData.data.data });
                if(cabData.data.data.type!='weekday'){
                    newStartDate=new Date(cabData.data.data.startDate);
                    newEndDate=new Date(cabData.data.data.endDate);
                    this.setState({isWeekday:false});
                }else{
                    this.setState({isWeekday:true});
                }
                
                //console.log(newStartDate+"===newStartDate="+cabData.data.data.startDate);
                
                this.setState({priceId:cabData.data.data._id});
                this.setState({cabType:cabData.data.data.cabType});
                this.setState({type:cabData.data.data.type});
                this.setState({startDate:newStartDate});
                this.setState({endDate:newEndDate});
                this.setState({weekday:cabData.data.data.weekDay});
                this.setState({rate:cabData.data.data.rate});
                this.setState({returTripRate:cabData.data.data.returnTripRate});
                this.setState({extraRate:cabData.data.data.extraRate});   
                
                this.setState({startDateSelected:cabData.data.data.startDate});
                this.setState({endDateSelected:cabData.data.data.endDate});             
                
            } else {

            }
        } catch (err) {
           
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    addPrice = async () => {
        let token = localStorage.getItem("token");
        
        const headers = { 'Authorization': `Bearer ${token}` };
        
        if (this.state.cabType == "" || this.state.cabType == null) {
            this.setState({ error: "Please select cab type" })
            return false;
        }
        if (this.state.type == "" || this.state.type == null) {
            this.setState({ error: "please select type" })
            return false;
        }else{
            if(this.state.type=='dateRange'){
                if (this.state.startDate == "" || this.state.startDate == null) {
                    this.setState({ error: "Please select start date" })
                    return false;
                }
                if (this.state.endDate == "" || this.state.endDate == null) {
                    this.setState({ error: "Please select end date" })
                    return false;
                }
            }else{
                if (this.state.weekday == "" || this.state.weekday == null) {
                    this.setState({ error: "Please select Day" })
                    return false;
                }
            }
        }

        
        if (this.state.rate == "" || this.state.rate == null) {
            this.setState({ error: "Please enter Rate" })
            return false;
        }
        if (this.state.returTripRate == "" || this.state.returTripRate == null) {
            this.setState({ error: "Please enter Return trip rate" })
            return false;
        }
        if (this.state.extraRate == "" || this.state.extraRate == null) {
            this.setState({ error: "Please enter Extra Km Rate" })
            return false;
        }
        
        let urlData={
            userId:this.state.userId,
            cabType:this.state.cabType,
            type:this.state.type,
            startDate:this.state.startDateSelected,
            endDate:this.state.endDateSelected,
            weekday:this.state.weekday,
            rate:this.state.rate,
            returTripRate:this.state.returTripRate,
            extraRate:this.state.extraRate,
        }
        try {
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}`,'Content-Type':'application/json' };
            
            let cabData = await axios.post(process.env.REACT_APP_APIURL + 'admin/add_specialprice' , urlData, { headers });
            
            if (cabData.data.code == 200) {
                //this.setState({ item: cabData.data.data });
                this.setState({ error: 'Price Added' });
                this.setState({cabType:''});
                this.setState({type:''});
                this.setState({startDate:''});
                this.setState({endDate:''});
                this.setState({startDateSelected:''});
                this.setState({endDateSelected:''});
                this.setState({weekday:0});
                this.setState({rate:0});
                this.setState({returTripRate:0});
                this.setState({extraRate:0});
                this.getSpecailPrice(this.state.userId);
            } else {
                this.setState({ error: 'Internal error' });
            }
        } catch (err) {
            
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });
        
    }
    
    editPrice = async () => {
        let token = localStorage.getItem("token");
        
        const headers = { 'Authorization': `Bearer ${token}` };
        if(this.state.priceId=='')
        {
            this.setState({ error: "Something went wrong" })
            return false;
        }
        if (this.state.cabType == "" || this.state.cabType == null) {
            this.setState({ error: "Please select cab type" })
            return false;
        }
        if (this.state.type == "" || this.state.type == null) {
            this.setState({ error: "please select type" })
            return false;
        }else{
            if(this.state.type=='dateRange'){
                if (this.state.startDate == "" || this.state.startDate == null) {
                    this.setState({ error: "Please select start date" })
                    return false;
                }
                if (this.state.endDate == "" || this.state.endDate == null) {
                    this.setState({ error: "Please select end date" })
                    return false;
                }
            }else{
                if (this.state.weekday == "" || this.state.weekday == null) {
                    this.setState({ error: "Please select Day" })
                    return false;
                }
            }
        }
        
        if (this.state.rate == "" || this.state.rate == null) {
            this.setState({ error: "Please enter Rate" })
            return false;
        }
        if (this.state.returTripRate == "" || this.state.returTripRate == null) {
            this.setState({ error: "Please enter Return trip rate" })
            return false;
        }
        if (this.state.extraRate == "" || this.state.extraRate == null) {
            this.setState({ error: "Please enter Extra Km Rate" })
            return false;
        }
        
        let urlData={
            priceId:this.state.priceId,
            userId:this.state.userId,
            cabType:this.state.cabType,
            type:this.state.type,
            startDate:this.state.startDateSelected,
            endDate:this.state.endDateSelected,
            weekday:this.state.weekday,
            rate:this.state.rate,
            returTripRate:this.state.returTripRate,
            extraRate:this.state.extraRate,
        }
        try {
            let token = localStorage.getItem("token");
           
            const headers = { 'Authorization': `Bearer ${token}`,'Content-Type':'application/json' };
            
            let cabData = await axios.put(process.env.REACT_APP_APIURL + 'admin/update_specialprice' , urlData, { headers });
            
            if (cabData.data.code == 200) {
                //this.setState({ item: cabData.data.data });
                this.setState({ error: 'Cab updated' });
                this.setState({cabType:''});
                this.setState({type:''});
                this.setState({startDate:''});
                this.setState({endDate:''});
                this.setState({startDateSelected:''});
                this.setState({endDateSelected:''});
                this.setState({weekday:0});
                this.setState({rate:0});
                this.setState({returTripRate:0});
                this.setState({extraRate:0});
                this.getSpecailPrice(this.state.userId);
            } else {
                this.setState({ error: 'Internal error' });
            }
        } catch (err) {
            
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
        this.setState({ isLoading: false });
    }
    onInputHandler(e) {
        const { name, value } = e.target;
        //this.setState({name: value});
    }
    setExtraRate = (rate) => {
        this.setState({ extraRate: rate.target.value });
    }
    setCabType = (cabTypes) => {
        this.setState({ cabType: cabTypes.target.value });
    }
    setRate = (rate) => {
        this.setState({ rate: rate.target.value });
    }
    setReturTripRate = (returTripRate) => {
        this.setState({ returTripRate: returTripRate.target.value });
    }
    
    setStartDate = (date) => {
        this.setState({startDate:date});
        let formattedDate=moment(date).format("YYYY-MM-DD");
        
        this.setState({ startDateSelected: formattedDate });
    }
    onEndTimeChange=(date)=>{     
        this.setState({returnTime:date});
        let formattedDate=moment(date).format("YYYY-MM-DD");
        
        this.setState({returnTimeSelected:formattedDate});
    }
    setEndDate = (endDate) => {
        this.setState({endDate:endDate});
        let formattedDate=moment(endDate).format("YYYY-MM-DD");
        
        this.setState({ endDateSelected: formattedDate });
    }
    setWeekday = (weekday) => {
        this.setState({ weekday: weekday.target.value });
    }
    
    setType = (type) => {
        if(type.target.value=='weekday'){
            this.setState({isWeekday:true})
        }else{
            this.setState({isWeekday:false})
        }
        
        this.setState({ type: type.target.value });
    }
    
    deletePrice=async(priceId)=>{
        try {
            this.setState({priceId:0})
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&priceId=" + priceId;
            
            let cabData = await axios.delete(process.env.REACT_APP_APIURL + 'admin/remove_specialprice?' + urlData, { headers });
            
            if (cabData.data.code == 200) {
                this.setState({ error: 'Price deleted successfully' });
                this.getSpecailPrice(this.state.userId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });
    }
    render() {
        const override = `
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (

            <div>
                <Header />
                <div>
                    <ClipLoader css={override} style={{ borderColor: 'red' }} color={this.state.loadingColor} loading={this.state.isLoading} size={150} >  </ClipLoader>
                </div>


                <div>
                    <section id="pricing" className="pricing">

                        <div className="container" data-aos="fade-up" style={{ width: '95%!important' }}>

                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>Add Special Price

                                        </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                {this.state.error}
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Cab Type <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <select className="form-control" value={this.state.cabType} onChange={this.setCabType}>
                                                            <option value="">Select Car Type</option>
                                                            {
                                                                this.state.cabTypes.map((object, i) => {
                                                                    return <option key={i} value={object.cabType}>{object.cabType}</option>
                                                                })
                                                            }
                                                        </select>
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail">
                                                        <Form.Label>Type<spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <select className="form-control" value={this.state.type} onChange={this.setType}>
                                                            <option value="">Select Type</option>
                                                            <option value="dateRange">dateRange</option>
                                                            <option value="weekday">weekday</option>
                                                        </select>
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            {
                                                this.state.isWeekday==false?<div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                    <div>
                                                        <Form.Group controlId="formBasicEmail" >
                                                            <Form.Label>Start Date <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                            <DatePicker popperPlacement="auto" dateFormat="yyyy-MM-dd" className="form-control"
                                                            selected={this.state.startDate}
                                                            minDate={new Date()}   
                                                            onChange={date => this.setStartDate(date)}                          
                                                            />
                                                            
                                                        </Form.Group>
                                                    </div>
                                                    <div>
                                                        <Form.Group controlId="formBasicEmail" >
                                                            <Form.Label>End Date <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                            
                                                            <DatePicker minDate={new Date()}
                                                            className="form-control"                                                            
                                                            popperPlacement="auto"
                                                            dateFormat="yyyy-MM-dd"                                                            
                                                            selected={this.state.endDate}
                                                            onChange={date => this.setEndDate(date)} 
                                                            />
                                                            
                                                        </Form.Group>
                                                    </div>
                                                </div>:null
                                            }
                                            
                                            {
                                                this.state.isWeekday==true?<div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                    <div>
                                                        <Form.Group controlId="formBasicEmail">
                                                            <Form.Label>Day<spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                            <select className="form-control" value={this.state.weekday} onChange={this.setWeekday}>
                                                                <option value="">Select Day</option>
                                                                <option value="0">Sunday</option>
                                                                <option value="1">Monday</option>
                                                                <option value="2">Tuesday</option>
                                                                <option value="3">Wednesday</option>
                                                                <option value="4">Thursday</option>
                                                                <option value="5">Friday</option>
                                                                <option value="6">Saturday</option>
                                                            </select>
                                                        </Form.Group>
                                                    </div>
                                                    
                                                </div>:null
                                            }
                                            

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Extra Rate Oneway <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="rate" type="text" placeholder="Rate" value={this.state.rate} onChange={this.setRate} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Extra Return Trip Rate <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="returTripRate" type="text" placeholder="Return trip rate" value={this.state.returTripRate} onChange={this.setReturTripRate} />
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Extra KM Rate <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="extraRate" type="text" placeholder="Car Model Name`" value={this.state.extraRate} onChange={this.setExtraRate} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12">
                                                {this.state.priceId<1?<Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    <Button variant="primary" type="button" onClick={this.addPrice.bind(this)}>
                                                        Save
                                                    </Button>
                                                </Form.Group>:<Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    <Button variant="primary" type="button" onClick={this.editPrice.bind(this)}>
                                                        Update
                                                    </Button>
                                                </Form.Group>
                                                }
                                                
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>
                            <br />
                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>Special Price </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                <Table striped bordered hover responsive>
                                                    <thead>
                                                        <tr>
                                                            <th>Cab Type</th>
                                                            <th>Type</th>
                                                            <th>Start Date</th>
                                                            <th>End Date</th>
                                                            <th>Week Day</th>
                                                            <th>Oneway Rate</th>
                                                            <th>Return Rate</th>
                                                            <th>Extra Rate </th>
                                                            <th>Action</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            this.state.item.map((object, i) => {
                                                                return <tr key={i}>
                                                                    <td>{object.cabType}</td>
                                                                    <td>{object.type}</td>
                                                                    <td>{object.startDate}</td>
                                                                    <td>{object.endDate}</td>
                                                                    <td>{this.state.weekDays[object.weekDay]}</td>
                                                                    <td>{object.rate}</td>
                                                                    <td>{object.returnTripRate}</td>
                                                                    <td>{object.extraRate}</td>
                                                                    <td>
                                                                        <Button style={{float:'right',marginRight:5}} variant="primary" type="button" onClick={this.viewPrice.bind(this,object._id)}>
                                                                            <i className="fa fa-edit iconStyle" aria-hidden="true"></i>
                                                                        </Button>&nbsp;&nbsp;
                                                                        <Button style={{float:'right',marginRight:5}} variant="primary" type="button" onClick={this.deletePrice.bind(this,object._id)}>
                                                                            <i className="fa fa-remove iconStyle" aria-hidden="true"></i>
                                                                        </Button>
                                                                        
                                                                    </td>
                                                                </tr>;
                                                            })
                                                        }

                                                    </tbody>
                                                </Table>
                                            </div>

                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>

                        </div>

                    </section>


                </div>
            </div>
        );
    }
}

export default withRouter(SpecialPrices);